<template>
  <section class="courseEdit">
    <div class="inner">
      <h1 class="tit">강의 수정</h1>
      <div class="snb">
        <course-nav />
      </div>
      <div class="board-box board-write">         
        <form id="inquiry" name="inquiry" method="" action="">
          <table>
            <caption class="tl">강의 계획서</caption>
            <colgroup>
              <col style="width:25%">
              <col style="width:75%">
            </colgroup>
            <tr>
              <th>강의 계획서</th>
              <td>
                <div id="filePicker">
                  <label for="file">
                    <span class="btn-upload inline btn ty2">파일 업로드하기</span>
                  </label>
                  <input type="file" name="file" id="file" @change="handleFileChange" multiple>
                  <ul id="output">
                    <li v-for="(file, index) in files" :key="index" class="inline mgr10">
                      {{ file.name }}
                      <button @click="removeFile(index)" type="button" class="file-del ty2">
                        <span class="material-symbols-outlined">close</span>
                      </button>
                    </li>
                  </ul>
                </div>
              </td>
            </tr>
          </table> 
        </form>
        <div class="ea tc">
          <button>저장</button>
        </div> 
      </div>          
    </div>
  </section>
</template>

<script>
import CourseNav from '@/components/lms/sub/course/CourseNav.vue';

export default {
  components: { CourseNav },
  data() {
    return {
      files: []
    }
  },
  methods: {
    handleFileChange(event) {
      this.files = Array.from(event.target.files).map(file => ({ name: file.name, data: file }));
    },
    removeFile(index) {
      this.files.splice(index, 1);
    }
  }
};
</script>

<style scoped lang="scss">
table {
  margin-top:5rem;
}
@media (max-width: #{$tablet - 1px}) {
  .board-box .w40 {
    width:100%;
  }
}
</style>